/*глобальные переменные в less */

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td,
menu {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li,
menu li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.relation {
  position: relative;
  border: 3px solid green;
  /*	вложенные классы - удобно для БЭM
	 &- контекст в данном случае = .relation */
}

.relation__ratio {
  height: 0;
  /*& = .relation__ratio */
}

.relation__ratio__16x9 {
  padding-top: 56.25%;
}

.relation__ratio__3x2 {
  padding-top: 67%;
}

.relation__ratio__1x1 {
  padding-top: 100%;
}

.relation__content {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}

/*миксины или функции в less - можно использовать в других местах стилей, 
чтобы не повторять код. Выполняют всю рутинную работу */

/*один столбец в сетке*/

/*Примесь для задания размера сетки */

/*Просто внешний контэйнер, ограничный по ширине на 1280px, выравнен по центру и
имеет внутренние отступы слева и справа */

.wrapper {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 30px;
}

/*ставим flex-grow: 1 и flex-basis: 100% для большой картинки */

.big {
  background-color: #fffd73;
  flex: 1 1 100%;
  width: 40%;
  /*Применяется блок стилей из grid.less*/
}

/*ставим flex-grow: 1 и flex-basis: 100% для блоков с 2-я маленькими картинками 
wrap тут не ставим, так как переносить картинки мы не хотим*/

.product {
  display: flex;
  flex-direction: column;
  flex: 1 1 100%;
  width: 30%;
}

.product1 {
  order: -1;
}

/*flex-basis: 50% - половина от родительского элемента */

.item {
  flex: 1 1 50%;
  /*в less можно писать media или псевдоклассы прямо внутри селекторов */
  /*& делает item:hover без пробелов*/
}

.item:hover {
  opacity: 0.8;
}

.item1 {
  background-color: #73C9FF;
}

.item2 {
  background-color: #7373FF;
}

.item3 {
  background: #7373FF;
}

.item4 {
  background: #73C9FF;
}

/*Для телефонов ставим flex-wrap: nowrap, чтобы элементы были в столбик
и убираем распорку позиционирования .relation__ratio */

.profit .row {
  display: flex;
  flex-wrap: wrap;
  margin: -15px;
}

.profit .row .item-row {
  margin-left: 15px;
  margin-right: 15px;
  box-sizing: border-box;
  width: calc(33.333333333333336% - 30px);
  text-align: center;
}

@media screen and (max-width: 780px) {
  .relation__ratio__16x9 {
    padding-top: 100%;
  }

  .big {
    width: 70%;
    background-color: #00ff00;
  }

  .product {
    flex: 1 1 50%;
  }

  .product1 {
    order: 0;
  }

  .profit .row {
    justify-content: center;
  }

  .profit .row .item-row {
    width: calc(50% - 30px);
  }
}

@media screen and (max-width: 560px) {
  .relation__ratio {
    display: none;
  }

  .relation__content {
    flex-wrap: nowrap;
    position: static;
  }

  .big,
  .product {
    width: auto;
  }
}